  // box-shadow: 0px 2  px 8px 3px rgb(223, 188, 255);

  &::before {
    inset: 0;
    content: "";
    z-index: -1;
    filter: brightness(35%);
    border-radius: 56% 44% 46% 54% / 41% 44% 56% 59%;
